/* single */
/* カテゴリーラベル */
.post .st-catgroup {
  padding-top: 0;
}
.catname {
  color: #fff;
  background: $highlight-color01;
  @include screen-pc {
    font-size: 11px !important;
  }
}

/* 投稿ページ */
.st-post {
}
.article-post {
  @include screen-pc {
    border: 2px solid $primary-color;
    border-radius: 10px;
    padding: 20px 40px;
    margin: 0 0 20px 0;
    box-shadow: 3px 3px 0px 0px $primary-color;
  }
  /* カテゴリと日付 */
  .article-post--header {
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    color: $txt-base-color01;
    @include screen-pc {
      flex-flow: row;
      align-items: center;
    }
    .article-post--header--date {
      margin: 15px 0 0 0;
      @include screen-pc {
        margin: 0 0 0 20px;
        font-size: 14px;
      }
    }
  }
  /* 見出し */
  .entry-title {
    line-height: 1.5;
    letter-spacing: 0.05em;
    // font-weight: normal;
    @include screen-pc {
      margin: 20px 0;
      border-bottom: 1px solid #ccc;
      padding-bottom: 20px;
      font-size: 18px;
    }
  }
  /* 本文 */
  .entry-content {
    line-height: 1.8;
    letter-spacing: 0.05em;
    @include screen-pc {
      font-size: 15px;
    }
    h2,h3 {
      font-size: 18px;
    }
    h2 {
      font-weight: bold;
      background: #f3f3f3;
      padding: 8px;
      border-left: 3px solid #ccc;
      margin: 20px 0;
    }
    h3 {
      padding: 0 10px 5px 0;
      font-size: 18px;
      line-height: 1.4;
      border-bottom: 1px dashed #8a8a8a;
    }
     h4 {
      background: none;
      padding: 0;
      margin-bottom: 10px;
     }
    p {
      margin-bottom: 20px;
    }
    table tr:nth-child(even) {
      background-color: transparent;
    }
    .post ol,
    .post ul {
      padding-top: 0;
    }
  }
}
.p-navi {
  margin-top: 20px;
  @include screen-pc {
    margin-top: 0;
  }
}
